import icons from './uni-icons/icons.js';
export default {
	data() {
		return {
			listAnimation: null,
			hoverItem:null,
			list:[
				{
					icon:'iconjurassic_apply',
					text:'全部文章'
				},
				{
					icon:'iconnpm',
					text:'我的npm'
				},
				{
					icon:'iconxiangmu',
					text:'项目库'
				},
				{
					icon:'iconruanjianxiazai',
					text:'软件分享'
				},
				{
					icon:'iconxiangce',
					text:'图片分享'
				},
				{
					icon:'iconresume-s',
					text:'我的简历'
				}
			]
		};
	},
	methods: {
		show() {
			let listanimation = uni.createAnimation({
			    duration: 300,
			    timingFunction: 'ease',
			})
			listanimation.translateX(0).step();
			setTimeout(()=>{
				this.listAnimation = listanimation.export()
			},0);
		},
		close() {
			let listanimation = uni.createAnimation({
			    duration: 300,
			    timingFunction: 'ease',
			})
			listanimation.translateX("-100%").step();
			setTimeout(()=>{
				this.listAnimation = listanimation.export()
			},0);
		},
		listClose(){
			this.$emit('close')
		},
		hover(e){
			let index = e.currentTarget.dataset.item
			this.hoverItem =index
			// this.$refs.item[index].$el.style.background='#f0f0f0'
		},
		start(e){
			let index = e.currentTarget.dataset.item
			this.hoverItem = null
		},
		anguagelTab(){
			//  语言切换
			let lg = this.language ==='zh'?'en':'zh'
			this._i18n.locale = lg
		}
	},
	computed:{
		language(){
			console.log(this._i18n)
			return this._i18n.locale
		}
	}
}
